.card {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff73;
}

.img {
  width: 100%;
  border-radius: 5px;
}

.title {
  font-size: 24px;
  margin-top: 10px;
}

.description {
  margin-top: 10px;
}

.year {
  margin-top: 10px;
  font-weight: bold;
}

.button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #39b300;
}
