body {
  background-color: rgb(91, 211, 248);
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.player {
  margin-bottom: 20px;
}

.player iframe {
  width: 100%;
  height: 360px;
  border: none;
}
@media (max-width: 768px) {
  .player iframe {
    height: 240px;
  }

  .movies {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}
